import React ,{ useState } from 'react'
import Footer from '../../components/ko/Footer'
import Header from '../../components/ko/Header'
import { Link } from 'react-router-dom';
import ContactModal from '../../components/ko/ContactModal';


const Introduce = () => {
  const [isOpenContact, setIsOpenContact] = useState(false);
  const onclickModal = () =>{
    setIsOpenContact(true)
  }
  return (
    <div className="wrap">
      <Header />

      <div id="content">
        <section id="introMain" className="sub_main">
          <div className="container">
            <div className="row">
              <div className="col-12">
                <p className="title wow fadeInDown" data-wow-delay="0.2s">ABOUT METAROCK</p>
                <h3 className="wow fadeInDown" data-wow-delay="0.4s"> E- <br />  .</h3>
              </div>
            </div>
          </div>
        </section>

        <section id="intro">
          <div className="container">
            <div className="row">
              <div className="col-lg-6 col-md-12 col-sm-12 wow fadeInLeft" data-wow-delay="0.2s">
              

                <h4>Metarock    <br className="pc" />     .</h4>
              </div>
              <div className="col-lg-6 col-md-12 col-sm-12 wow fadeInRight" data-wow-delay="0.4s">
                <p>       ,    . , VR, AI, Entertainment, E-Commerce        , NFT, P2E,            DotCube Protocol       .

                </p>
                <div className="ceo wow fadeInRight" data-wow-delay="0.6s">  <span> </span></div>
              </div>
            </div>
          </div>
        </section>

        <section id="business" className="sub_cont">
          <div className="container">
            <div className="row">
              <div className="col-12">
                <p className="title wow fadeInDown" data-wow-delay="0.2s">METAROCK </p>
                <h4 className="wow fadeInDown" data-wow-delay="0.4s">  </h4>
                <p className="text wow fadeInDown" data-wow-delay="0.6s">  .    .</p>
              </div>
              <div className="col-lg-3 col-md-6 col-sm-12 wow fadeInLeft" data-wow-delay="0.8s">
                <div className="box">
                  <Link to="/game" title="">
                    <div className="thumb">

                      <img src={`${process.env.PUBLIC_URL}/images/business/business1.png`} alt="2WinChance" />
                    </div>
                    <div className="cont">
                      <h5>2WinChance</h5>
                      <p> <i className="far fa-chevron-right"></i></p>
                    </div>
                  </Link>
                </div>
              </div>
              <div className="col-lg-3 col-md-6 col-sm-12 wow fadeInLeft" data-wow-delay="0.9s">
                <div className="box">
                  <Link to="/metaverse" title="">
                    <div className="thumb">
                      <img src={`${process.env.PUBLIC_URL}/images/business/business2.png`} alt="" />
                    </div>
                    <div className="cont">
                      <h5>DotCube Protocol</h5>
                      <p> <i className="far fa-chevron-right"></i></p>
                    </div>
                  </Link>
                </div>
              </div>
              <div className="col-lg-3 col-md-6 col-sm-12 wow fadeInLeft" data-wow-delay="1.0s">
                <div className="box">
                  <Link to="/heaven" title="">
                    <div className="thumb">
                      <img src={`${process.env.PUBLIC_URL}/images/business/business3.png`} alt="Heaven" />
                    </div>
                    <div className="cont">
                      <h5>Heaven</h5>
                      <p> <i className="far fa-chevron-right"></i></p>
                    </div>
                  </Link>
                </div>
              </div>
              <div className="col-lg-3 col-md-6 col-sm-12 wow fadeInLeft" data-wow-delay="1.1s">
                <div className="box">
                  <Link to="/mozza" title="">
                    <div className="thumb">
                      <img src={`${process.env.PUBLIC_URL}/images/business/business4.png`} alt="Mozza & Sali" />
                    </div>
                    <div className="cont">
                      <h5>Mozza &amp; Sali</h5>
                      <p> <i className="far fa-chevron-right"></i></p>
                    </div>
                  </Link>
                </div>
              </div>
            </div>
          </div>
        </section>

        <section id="organization" className="sub_cont">
          <div className="container">
            <div className="row">
              <div className="col-12">
                <p className="title wow fadeInDown" data-wow-delay="0.2s">Organization Chart</p>
                <h4 className="wow fadeInDown" data-wow-delay="0.4s"> </h4>
                <img src={`${process.env.PUBLIC_URL}/images/organization/organization.png?ver=20221207`} alt=" " className="pc_img wow fadeInUp"
                  data-wow-delay="0.8s" />
                <img src={`${process.env.PUBLIC_URL}/images/organization/organization_m.png?ver=20221207`} alt=" " className="m_img wow fadeInUp"
                  data-wow-delay="0.8s" />
              </div>
            </div>
          </div>
        </section>

        <section id="introContact" className="sub_contact">
          <div className="container">
            <div className="row">
              <h4 className="wow fadeInDown" data-wow-delay="0.4s">  ?</h4>
              {/* <button
                title="contact" 
                className="btn_contact wow fadeInUp"
                data-wow-delay="0.8s"
                onClick={() => {
                  onclickModal()
                }}
              >
                CONTACT US
              </button> */}
                <Link to={"/contact"} className="btn_contact wow fadeInUp" data-wow-delay="0.8s">CONTACT US</Link>
              
            </div>
          </div>
        </section>
      </div>
      {isOpenContact ? (
              <ContactModal setIsOpenContact={setIsOpenContact} />
      ) : null}

      <footer>
        <div className="container">
          <Footer />
          
        </div>
      </footer>
    </div>
  )
}

export default Introduce