import { useEffect, useState } from "react"
import storage from "../../utils/storage"

const Modal = () => {
  const [isShow, setIsShow] = useState<boolean>(true)

  useEffect(()=>{
    const today = new Date().getTime()
    const popupState = storage.getItem('popup', true)
    if(today > popupState){
      storage.removeItem('popup')
      setIsShow(true)
    }else{
      setIsShow(false)
    }
  },[])

  const closeModal = () =>{
    setIsShow(false)
  }

  const hideModal = () => {
    const today = new Date()
    storage.setItem('popup', new Date(today.setDate(today.getDate()+1)).getTime())
    setIsShow(false)
  }

  return (
    <div id="popup_layer">
      {
        isShow? 
        <div >
            <div id="layer_popup" className="row">
           
              <h1>  ()</h1>
              <div className="layer_scroll">
                <h2> :   ( )</h2>
                <p style={{lineHeight: '15px'}}> 416  2024 2 5        . </p>

            <p style={{textAlign:'center'}}>-<span style={{padding:'0px 10px'}}>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;</span>-</p>
            <p style={{lineHeight: '5px'}}>1.    :  6,000,000</p>
            <p style={{lineHeight: '5px'}}>2.   :  </p>
            <p style={{lineHeight: '5px'}}>3.   : 1  500 (: 1 100)</p>
            <p style={{lineHeight: '5px'}}>4. : 2024 2 22</p>
            <p style={{lineHeight: '15px'}}>5.   :   9 1               ,     ,          .</p>
            <p style={{lineHeight: '5px'}}>6. : 2024 3 25</p>
            <p style={{lineHeight: '5px'}}>7.  : </p>
            <p style={{lineHeight: '5px'}}>8. : 2024 3 29 </p>
            <p style={{lineHeight: '5px'}}>9.    :  </p>
            <p style={{lineHeight: '5px'}}>10.   :  .</p>
            <p style={{lineHeight: '15px'}}>11.  :                .</p> 
            <p style={{lineHeight: '5px'}}>12.       .</p>
                <p style={{ lineHeight: '5px' }}>&nbsp;</p>    
            
             <h2 style={{textAlign:'center'}}>     </h2>
             <p style={{lineHeight: '15px'}}> 354    9         ,     ,       .</p>
                    

         <p style={{textAlign:'center'}}>-<span style={{padding:'0px 10px'}}>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;</span>-</p>
         <p style={{lineHeight: '5px'}}>1.  (): 2024 2 22</p>
         <p style={{lineHeight: '5px'}}>2.  : 2024 2 23 ~ 2024 2 26</p>

            <p style={{ textAlign: 'center', fontWeight: 'bold' }}>2024 2 15
            </p>
            <p style={{ textAlign: 'center', fontWeight: 'bold' }}>   ()
            </p>
              </div>
              {/* <img src={`${process.env.PUBLIC_URL}/images/logo/logo.png`} /> */}
              <div className="medal_btn">
              <button className="btn_one" onClick={hideModal}>  </button>
              <button className="close_btn" onClick={closeModal}></button>
              </div>
          </div >
        </div >
        :null
      }
    </div >
  )
}

export default Modal