import React, { useState } from 'react'
import { Swiper, SwiperSlide } from 'swiper/react';
import { Autoplay, EffectFade} from "swiper";
import 'swiper/css/autoplay';
import "swiper/css/effect-fade";
import Footer from '../../components/ko/Footer'
import Header from '../../components/ko/Header'
import ContactModal from '../../components/ko/ContactModal'
import { Link } from "react-router-dom";

const Mozza = () => {
  const [isOpenContact, setIsOpenContact] = useState(false);
  
  const onclickModal = () =>{
    setIsOpenContact(true)
  }
  return (
    <div className="wrap">
      <Header />
      <div id="content">
        <section id="mozzaMain" className="sub_main">
          <div className="container">
            <div className="row">
              <div className="col-12">
                <p className="title wow fadeInDown" data-wow-delay="0.2s">BUSINESS  Mozza &amp; Sali</p>
                <h3 className="wow fadeInDown" data-wow-delay="0.4s">Mozza &amp; Sali<br />Full CG 3D Animation
                </h3>
              </div>
            </div>
            <Swiper
              autoplay={{
                delay: 4000,
                disableOnInteraction: false,
              }}
              centeredSlides={true}
              modules={[Autoplay, EffectFade]}
              effect={"fade"}
              spaceBetween={50}
              slidesPerView={1}
              speed={3000}
            >
              <SwiperSlide></SwiperSlide>
              <SwiperSlide></SwiperSlide>
              <SwiperSlide></SwiperSlide>
            </Swiper>
          </div>
        </section>

        <section id="storyline" className="sub_cont">
          <div className="container">
            <div className="row">
              <div className="col-12">
                <p className="title wow fadeInDown" data-wow-delay="0.2s">Storyline</p>
                <h4 className="wow fadeInDown" data-wow-delay="0.4s">  <br className="m" />  </h4>
                <p className="text wow fadeInDown" data-wow-delay="0.6s">       
                  ( )   , <br className="pc" />      .</p>
                <img src={`${process.env.PUBLIC_URL}/images/mozza/storyline.png`} alt="Character Design" className="wow fadeInUp"
                  data-wow-delay="0.8s" />
                <p className="text wow fadeInUp" data-wow-delay="1.0s">Character Design</p>
              </div>
            </div>
          </div>
        </section>

        <section id="mozza">
          <div className="container">
            <div className="row fir">
              <div className="col-lg-6 col-md-12 col-sm-12 wow fadeInLeft" data-wow-delay="0.2s">
                <img src={`${process.env.PUBLIC_URL}/images/mozza/mozza1.png`} alt="  (Vivid Tone)" />
              </div>
              <div className="col-lg-6 col-md-12 col-sm-12 wow fadeInRight" data-wow-delay="0.4s">
                <p>Color Concept</p>
                <dl>
                  <dt>  (Vivid Tone)</dt>
                  <dd>   ,         (Dynamic), (Intensity),
                    (Abundant)   (Vivid Tone) .</dd>
                </dl>
              </div>
            </div>
            <div className="row sec">
              <div className="col-lg-6 col-md-12 col-sm-12 right wow fadeInRight" data-wow-delay="0.6s">
                <img src={`${process.env.PUBLIC_URL}/images/mozza/mozza2.png`} alt="    " />
              </div>
              <div className="col-lg-6 col-md-12 col-sm-12 wow fadeInLeft" data-wow-delay="0.8s">
                <p>Tone &amp; Manner</p>
                <dl>
                  <dt>    </dt>
                  <dd>    Low Key Medium Key   Tone.     BGM, Sound,
                    SFX</dd>
                </dl>
              </div>
            </div>
          </div>
        </section>

        <section id="plan" className="sub_cont">
          <div className="container">
            <div className="row">
              <div className="col-12">
                <p className="title wow fadeInDown" data-wow-delay="0.2s">Business plan</p>
                <h4 className="wow fadeInDown" data-wow-delay="0.4s">  <br className="m" />    
                </h4>
                <p className="text wow fadeInDown" data-wow-delay="0.6s">Adulkid Kidult      
                  ,      <br className="pc" />    .</p>
                <img src={`${process.env.PUBLIC_URL}/images/mozza/plan.png`} alt="      " className="wow fadeInUp"
                  data-wow-delay="0.8s" />
                <p className="text wow fadeInUp" data-wow-delay="1.0s">Mozza &amp; Sali  OSMU,
                  Collaboration, Co Work       <br className="pc" />    , 
                     .</p>
              </div>
            </div>
          </div>
        </section>

        <section id="mozzaContact" className="sub_contact">
          <div className="container">
            <div className="row">
              <h4 className="wow fadeInDown" data-wow-delay="0.4s">METAROCK  ?</h4>
              {/* <button
    title="contact" 
    className="btn_contact wow fadeInUp"
    data-wow-delay="0.8s"
    onClick={() => {
      onclickModal()
    }}
  >
    CONTACT US
      </button> */}
              <Link to={"/contact"} className="btn_contact wow fadeInUp" data-wow-delay="0.8s">CONTACT US</Link>
            </div>
          </div>
        </section>
      </div>
      {isOpenContact ? (
              <ContactModal setIsOpenContact={setIsOpenContact} />
            ) : null}
      <footer>
        <div className="container">
          <Footer />
        </div>
      </footer>
    </div >
  )
}

export default Mozza