import React, { useEffect, useState } from "react";
import Footer from "./Footer";
import Header from "./Header";
import { Link } from "react-router-dom";
import { useCookies } from "react-cookie";
import moment from "moment";
import { get_boardList } from "../../utils/axios";
import ContactModal from "./ContactModal";



const Main = () => {
  const [newsData, setNewsData] = useState([]);
  const [page, setPage] = useState(1);
  const [totalPage, setTotalPage] = useState(1);

  const [isOpenContact, setIsOpenContact] = useState(false);
  

  const onclickModal = () =>{
    setIsOpenContact(true)
  }

  useEffect(() => {
    (async () => {
      const params = {
        page: page,
        size: 10,
        boardIdx: 1,
      };
      try {
        const { data } = await get_boardList(params);
        setTotalPage(data.totalPages);
        setNewsData(data.List.content);
      } catch (err) {
        console.error(err);
      }
    })();
  }, [page]);

  const openDialog = (articleIdx: number, externalUrl: string) => {
    switch (articleIdx) {
      default:
        window.open(externalUrl);
        break;
    }
  };

  return (
    <div style={{ position: "relative" }}>
      <Header />

      <div id="popup_layer" className="container"></div>
      <div className="wrap">
        <div id="content">
          <section id="main">
            <div className="container">
              <video title="matavideo" muted autoPlay loop>
                <source
                  src={process.env.PUBLIC_URL + "video/mainvideo.mp4"}
                  type="video/mp4"/>
              </video>
              <div className="row">
                <div className="col-12">
                  <h3 className="wow fadeInDown" data-wow-delay="0.4s">
                    The Way to the Metaverse. <br />
                    Meet us, Metarock
                  </h3>
                  <p className="wow fadeInDown" data-wow-delay="0.8s">
                    METAROCK  
                    <br />
                    E-     .
                  </p>
                  {/* <button title="contact" className="btn_contact wow fadeInUp" data-wow-delay="0.8s" onClick={()=> {
                    onclickModal()
                    }}
                    >
                    CONTACT US
                  </button> */}
                  <Link to={"/contact"} className="btn_contact wow fadeInUp" data-wow-delay="0.8s">CONTACT US</Link>
                </div>
              </div>
            </div>
          </section>

          <section id="service">
            <div className="container">
              <div className="row">
                <div className="col-12">
                  <p className="title wow fadeInDown" data-wow-delay="0.2s">
                    Our Services
                  </p>
                  <h4 className="wow fadeInDown" data-wow-delay="0.4s">
                    METAROCK  
                  </h4>
                </div>
              </div>
              <div className="row fir">
                <div
                  className="col-lg-6 col-md-12 col-sm-12 wow fadeInLeft"
                  data-wow-delay="0.6s"
                >
                  <img
                    src="images/service/service1.png"
                    alt="2WinChance,    "
                  />
                </div>
                <div
                  className="col-lg-6 col-md-12 col-sm-12 wow fadeInLeft"
                  data-wow-delay="0.7s"
                >
                  <dl>
                    <dt>2WinChance,       </dt>
                    <dd>
                    2WinChance       WINNERPICK   ,     P2E      .

                    </dd>
                    <dd>
                      <Link to={"/game"} title="" className="btn_more">
                         
                      </Link>
                    </dd>
                  </dl>
                </div>
              </div>
              <div className="row cross sec">
                <div
                  className="col-lg-6 col-md-12 col-sm-12 right wow fadeInRight"
                  data-wow-delay="0.8s"
                >
                  <img
                    src="images/test/service2.png"
                    alt="  "
                  />
                </div>
                <div
                  className="col-lg-6 col-md-12 col-sm-12 wow fadeInRight"
                  data-wow-delay="0.9s"
                >
                  <dl>
                    <dt>   , DotCube Protocol(DCP)</dt>
                    <dd>
                         DotCube Protocol          ,          .

                    </dd>
                    <dd>
                      {" "}
                      <Link
                        to={"/metaverse"}
                        title=""
                        className="btn_more"
                      >
                         
                      </Link>
                    </dd>
                  </dl>
                </div>
              </div>
              <div className="row thi">
                <div
                  className="col-lg-6 col-md-12 col-sm-12 wow fadeInLeft"
                  data-wow-delay="1.0s"
                >
                  <img
                    src="images/service/service3.png"
                    alt="Heaven,     "
                  />
                </div>
                <div
                  className="col-lg-6 col-md-12 col-sm-12 wow fadeInLeft"
                  data-wow-delay="1.1s"
                >
                  <dl>
                    <dt>Heaven,     </dt>
                    <dd>
                      Heaven DOTA/CHAOS      
                      Aeon Of Strife(AOS) .
                    </dd>
                    <dd>
                      {" "}
                      <Link to={"/heaven"} title="" className="btn_more">
                         
                      </Link>
                    </dd>
                  </dl>
                </div>
              </div>
              <div className="row cross for">
                <div
                  className="col-lg-6 col-md-12 col-sm-12 right wow fadeInRight"
                  data-wow-delay="1.2s"
                >
                  <img
                    src="images/service/service4.png"
                    alt="Mozza & Sali Full CG 3D Animation"
                  />
                </div>
                <div
                  className="col-lg-6 col-md-12 col-sm-12 wow fadeInRight"
                  data-wow-delay="1.3s"
                >
                  <dl>
                    <dt>Mozza &amp; Sali Full CG 3D Animation</dt>
                    <dd>
                      Adulkid Kidult      
                           .
                    </dd>
                    <dd>
                      {" "}
                      <Link
                        to={"/mozza"}
                        title=""
                        className="btn_more"
                      >
                         
                      </Link>
                    </dd>
                  </dl>
                </div>
              </div>
            </div>
          </section>

          <section id="mainNews">
            <div className="container">
              <div className="row">
                <div className="col-12">
                  <p className="title wow fadeInDown" data-wow-delay="0.2s">
                    Our News
                  </p>
                  <h4 className="wow fadeInDown" data-wow-delay="0.4s">
                    METAROCK NEWS
                  </h4>
                </div>
                <div className="row">
                  {newsData.map(
                    (
                      {
                        thumbnailUrl,
                        articleSubject,
                        externalAuthor,
                        regDt,
                        articleContent,
                        externalUrl,
                        articleIdx,
                      },
                      i
                    ) => {
                      return (
                        <div
                          key={i}
                          className="col-lg-4 col-md-6 col-sm-12 wow fadeInLeft"
                          data-wow-delay="0.65s"
                          onClick={() => {
                            openDialog(articleIdx, externalUrl);
                          }}
                        >
                          {i < 6 ? (
                            <a target="_blank" rel="noreferrer">
                              <div className="box">
                                <div className="thumb">
                                  <img
                                    src={thumbnailUrl}
                                    alt={articleSubject}
                                  />
                                </div>
                                <div className="cont">
                                  <p className="sub">
                                    {externalAuthor}. {regDt}
                                  </p>
                                  <dl>
                                    <dt>{articleSubject}</dt>
                                    <dd> {articleContent}</dd>
                                  </dl>
                                </div>
                              </div>
                            </a>
                          ) : null}
                        </div>
                      );
                    }
                  )}
                </div>
                <div className="col-12 wow fadeInUp" data-wow-delay="0.9s">
                  <Link to={"/news"} className="btn_more">
                     <i className="fal fa-plus"></i>
                  </Link>
                </div>
              </div>
            </div>
          </section>

          <section id="contact">
            <div className="container">
              <div className="row">
                <div className="col-12">
                  <h4 className="wow fadeInDown" data-wow-delay="0.4s">
                      ?
                  </h4>
                  {/* <button title="contact" className="btn_contact wow fadeInUp" data-wow-delay="0.8s" onClick={()=> {
                    onclickModal()
                    }}
                    >
                    CONTACT US
                  </button> */}
                  <Link to={"/contact"} className="btn_contact wow fadeInUp" data-wow-delay="0.8s">CONTACT US</Link>
                </div>
              </div>
            </div>


   
          </section>
        </div>
        {isOpenContact ? (
              <ContactModal setIsOpenContact={setIsOpenContact} />
        ) : null}

        <footer>
          <div className="container">
            <div className="row">
              <Footer />
            </div>
          </div>
        </footer>
      </div>
    </div>
  );
};

export default Main;
