import React from 'react'
import Footer from '../../components/ko/Footer'
import Header from '../../components/ko/Header'


const Sns = () => {
  return (
    <div className="wrap">
      <Header />
      <div id="content2">
        <section id="snsMain" className="sub_main">
          <div className="container">
            <div className="row">
              <div className="col-12">
                <p className="title wow fadeInDown" data-wow-delay="0.2s">PR CENTER  </p>
                <h3 className="wow fadeInDown" data-wow-delay="0.4s">   </h3>
              </div>
            </div>
          </div>
        </section>

        <section id="sns">
          <div className="container">
            <div className="row">
              <div className="col-lg-4 col-md-6 col-sm-12">
                <div className="box">
                  <a onClick={() => window.open('https://twitter.com/metarock143', '_blank')}title="" >
                    <img src={`${process.env.PUBLIC_URL}/images/sns/twitter.png`} alt="TWITTER" />
                    <p>TWITTER</p>
                  </a>
                </div>
              </div>
              <div className="col-lg-4 col-md-6 col-sm-12">
                <div className="box">
                  <a onClick={() => window.open('https://www.facebook.com/%EB%A9%94%ED%83%80%EB%A1%9D-102198669046522', '_blank')}
                    title="">
                    <img src={`${process.env.PUBLIC_URL}/images/sns/facebook.png`} alt="FACEBOOK" />
                    <p>FACEBOOK</p>
                  </a>
                </div>
              </div>
              <div className="col-lg-4 col-md-6 col-sm-12">
                <div className="box">
                  <a onClick={() => window.open('https://www.instagram.com/metarock_dcp/', '_blank')} title="">
                    <img src={`${process.env.PUBLIC_URL}/images/sns/instagram.png`} alt="INSTAGRAM" />
                    <p>INSTAGRAM</p>
                  </a>
                </div>
              </div>
              <div className="col-lg-4 col-md-6 col-sm-12">
                <div className="box">
                  <a onClick={() => window.open('https://www.youtube.com/@Metarock_Official', '_blank')}
                    title="" >
                    <img src={`${process.env.PUBLIC_URL}/images/sns/youtube.png`} alt="YOUTUBE" />
                    <p>YOUTUBE</p>
                  </a>
                </div>
              </div>
              <div className="col-lg-4 col-md-6 col-sm-12">
                <div className="box">
                  <a href="#" className="btn_wait">
                    <img src={`${process.env.PUBLIC_URL}/images/sns/comingsoon.png`} alt="" />
                    <p></p>
                  </a>
                </div>
              </div>
            </div>
          </div>
        </section>
      </div>

      <footer>
        <div className="container">
          <Footer />
        </div>
      </footer>
    </div>
  )
}

export default Sns