import { Dispatch, SetStateAction } from "react";

const ContactModal = ({
  setIsOpenContact,
}: {
  setIsOpenContact: Dispatch<SetStateAction<boolean>>;
}) => {
  const closeContact = () => {
    setIsOpenContact(false);
  };

  return (
    <div id="popup_layer" className="contact_popup">
        <div>
        <div className="layer_popup_header">
        <h1>Contact Us</h1>
        </div>      
      <div id="Contactlayer_popup" className="row">
     
        <div className="layer_scroll">
          <form>
            <label>
              <span className="title"><sup>*</sup></span>
              <input type="text" />
            </label>
            <label>
              <span className="title"></span>
              <input type="text" />
            </label>
            <label>
              <span className="title"><sup>*</sup></span>
              <input type="text" />
            </label>
            <label>
              <span className="title"></span>
              <input type="text" />
            </label>
            <label>
              <span className="title"></span>
              <input type="text" />
            </label>
            <label>
              <span className="title"><sup>*</sup></span>
              <input type="text" />
            </label>
            <label className="Category">
              <span className="title"><sup>*</sup></span>
                <select className="Category_select">
                  <option> </option>
                  <option> </option>
                  <option> </option>
                  <option> </option>
                  <option></option>
              </select>
            </label>
            <label>
              <span className="title"><sup>*</sup></span>
              <input type="text" />
            </label>
            <label className="ContText">
                <span className="title"><sup>*</sup></span>
                <textarea placeholder=" ." id="contentText" name="contentText" required>
                  
                </textarea>
            </label>
            <label>
              <span className="title"></span>
              <input type="file" className="browse" />
              </label>
              <label className="Contactalert">
                  /    .
              </label>
        </form>
          

        </div>
        <div className="Contactmedal_btn">
        <button className="send_btn"></button>
        <button className="close_btn" onClick={closeContact}></button>
        </div>
    </div >
  </div >

    </div >
  )
}

export default ContactModal;