import React , { useEffect } from 'react';
import { Link } from 'react-router-dom';
import $ from 'jquery'

const Footer = () => {
  useEffect(() => {
    $("footer .fnb li dd>a").click(function (e) {
      e.stopImmediatePropagation();

    })
  }, []);
  return (
    <div className="container">
      <div className="row">
        <div className="col-12">
          <ul className="fnb clear">
            <li>
              <dl>
                <dt className="wow fadeInLeft" data-wow-delay="0.4s">METAROCK</dt>
                <dd className="wow fadeInLeft" data-wow-delay="0.45s"><Link to={'/introduce'} title="" ></Link></dd>
                <dd className="wow fadeInLeft" data-wow-delay="0.5s"><Link to={'/history'} title="">History</Link></dd>
                <dd className="wow fadeInLeft" data-wow-delay="0.55s"><Link to={'/patent'} title="">  </Link></dd>
                <dd className="wow fadeInLeft" data-wow-delay="0.6s"><Link to={'/partner'} title=""></Link></dd>
              </dl>
            </li>
            <li>
              <dl>
                <dt className="wow fadeInLeft" data-wow-delay="0.6s">BUSINESS</dt>
                <dd className="wow fadeInLeft" data-wow-delay="0.7s"><Link to={'/metaverse'} title="">DotCube Protocol</Link></dd>
                <dd className="wow fadeInLeft" data-wow-delay="0.65s"><Link to={'/game'} title="">2WinChance</Link></dd>
                <dd className="wow fadeInLeft" data-wow-delay="0.75s"><Link to={'/heaven'} title="">Heaven</Link></dd>
                <dd className="wow fadeInLeft" data-wow-delay="0.8s"><Link to={'/mozza'} title="">Mozza &amp; Sali</Link></dd>
              </dl>
            </li>
            <li>
              <dl>
                <dt className="wow fadeInLeft" data-wow-delay="0.85s">PR CENTER</dt>
                <dd className="wow fadeInLeft" data-wow-delay="0.9s"><Link to={'/notice'} title=""></Link></dd>
                <dd className="wow fadeInLeft" data-wow-delay="0.9s"><Link to={'/news'} title=""> </Link></dd>
                <dd className="wow fadeInLeft" data-wow-delay="0.95s"><Link to={'/sns'} title=""></Link></dd>
              </dl>
            </li>
            <li>
              <dl>
                <dt className="wow fadeInLeft" data-wow-delay="1.0s">CAREERS</dt>
                <dd className="wow fadeInLeft" data-wow-delay="1.05s"><Link to={'/hire'} title=""></Link></dd>
                <dd className="wow fadeInLeft" data-wow-delay="1.15s"><Link to={'/faq'} title="faq"> FAQ</Link></dd>
              </dl>
            </li>
            <li>
              <dl>
                <dt className="wow fadeInLeft" data-wow-delay="1.2s"><Link to={'/contact'} title="contact">CONTACT</Link></dt>
              </dl>
            </li>
          </ul>
        </div>
      </div>
      <p className="copy wow fadeInUp" data-wow-delay="1.25s">COPYRIGHT &copy; 2022. Metarock Inc. All Right Reserved.</p>
    </div>

  )
}

export default Footer