import React from 'react'
import Footer from '../../components/ko/Footer'
import Header from '../../components/ko/Header'

const Patent = () => {
  return (
    <div className="wrap">
      <Header />
      <div id="content2">
        <section id="patentMain" className="sub_main">
          <div className="container">
            <div className="row">
              <div className="col-12">
                <p className="title wow fadeInDown" data-wow-delay="0.2s">METAROCK   /  </p>
                <h3 className="wow fadeInDown" data-wow-delay="0.4s">    </h3>
              </div>
            </div>
          </div>
        </section>

        <section id="patent" className="sub_cont">
          <div className="container">
            <div className="row">
              <div className="col-12">
                <h4 className="wow fadeInDown" data-wow-delay="0.4s">   </h4>
              </div>
              <div className="col-lg-4 col-md-6 col-sm-12 wow fadeInLeft" data-wow-delay="0.6s">
                <div className="thumb">
                  <img src={`${process.env.PUBLIC_URL}/images/patent/patent1.png`} alt="    " />
                </div>
              </div>
              <div className="col-lg-4 col-md-6 col-sm-12 wow fadeInLeft" data-wow-delay="0.8s">
                <div className="thumb">
                  <img src={`${process.env.PUBLIC_URL}/images/patent/patent2.png`} alt="    " />
                </div>
              </div>
              <div className="col-lg-4 col-md-6 col-sm-12 wow fadeInLeft" data-wow-delay="1.0s">
                <div className="thumb">
                  <img src={`${process.env.PUBLIC_URL}/images/patent/patent3.png`} alt="     " />
                </div>
              </div>
              <div className="col-12 license">

                <h4 className="wow fadeInDown" data-wow-delay="0.4s">  </h4>
              </div>
              <div className="col-lg-4 col-md-6 col-sm-12 wow fadeInLeft" data-wow-delay="1.1s">
                <div className="thumb">
                  <img src={`${process.env.PUBLIC_URL}/images/patent/patent4.png`} alt="" />
                </div>
              </div>
              <div className="col-lg-4 col-md-6 col-sm-12 wow fadeInLeft" data-wow-delay="1.2s">
                <div className="thumb">
                  <img src={`${process.env.PUBLIC_URL}/images/patent/patent5.png`} alt="" />
                </div>
              </div>
              <div className="col-lg-4 col-md-6 col-sm-12 wow fadeInLeft" data-wow-delay="1.3s">
                <div className="thumb">
                  <img src={`${process.env.PUBLIC_URL}/images/patent/patent6.png`} alt="" />
                </div>
              </div>
            </div>
          </div>
        </section>
      </div>
      <footer>
        <div className="container">
          <Footer />
        </div>
      </footer>
    </div>
  )
}

export default Patent