import React ,{useEffect,useState} from 'react';
import { Swiper, SwiperSlide } from 'swiper/react';

import 'swiper/css';
import { Autoplay, EffectFade } from "swiper";
import 'swiper/css/autoplay';
import "swiper/css/effect-fade";
import Header from '../../components/ko/Header';
import Footer from '../../components/ko/Footer';
import ContactModal from '../../components/ko/ContactModal';
import { Link } from "react-router-dom";
import $ from 'jquery'


const Game = () => {
  const [isOpenContact, setIsOpenContact] = useState(false);
  const onclickModal = () =>{
    setIsOpenContact(true)
  }
  useEffect(()=>{
    $("#game .fir .col-lg-4 li").click(function (this, e)  {
      e.preventDefault();
      $(this).addClass("on").siblings().removeClass("on");
  
      if ($("#game .fir .col-lg-4 li:eq(0)").hasClass("on")) {
        $(this)
          .parents(".col-lg-4")
          .siblings(".col-lg-8")
          .children("img")
          .attr("src", "images/test/game1.jpg");
        $(this)
          .parents(".col-lg-4")
          .siblings(".col-lg-8")
          .children("img")
          .attr("alt", "Esports Betting Game ");
      } else if ($("#game .fir .col-lg-4 li:eq(1)").hasClass("on")) {
        $(this)
          .parents(".col-lg-4")
          .siblings(".col-lg-8")
          .children("img")
          .attr("src", "images/test/game2.jpg");
        $(this)
          .parents(".col-lg-4")
          .siblings(".col-lg-8")
          .children("img")
          .attr("alt", "Pro Esports     ");
      } else {
        $(this)
          .parents(".col-lg-4")
          .siblings(".col-lg-8")
          .children("img")
          .attr("src", "images/game/game3.png");
        $(this)
          .parents(".col-lg-4")
          .siblings(".col-lg-8")
          .children("img")
          .attr("alt", " Esports News Journal ");
      }
    });
  },[])
  return (
    <div className="wrap">
      <Header />
      <div id="content">
        <section id="gameMain" className="sub_main">
          <div className="container">
            <div className="row">
              <div className="col-12">
                <p className="title wow fadeInDown" data-wow-delay="0.2s">BUSINESS  2WinChance</p>
                <h3 className="wow fadeInDown" data-wow-delay="0.4s">2WinChance,<br /> Esports  
                  </h3>
                <p className="text wow fadeInDown" data-wow-delay="0.6s">2WinChance    Esports 
                          .</p>
              </div>
            </div>


            <Swiper
              autoplay={{
                delay: 4000,
                disableOnInteraction: false,
              }}
              centeredSlides={true}
              modules={[Autoplay, EffectFade]}
              effect={"fade"}
              spaceBetween={50}
              slidesPerView={1}
              speed={2000}
            >
              <SwiperSlide></SwiperSlide>
              <SwiperSlide></SwiperSlide>
              <SwiperSlide></SwiperSlide>
            </Swiper>


          </div>
        </section>

        <section id="game" className="sub_cont">
          <div className="container">
            <div className="row fir">
              <div className="col-12">
                <p className="title wow fadeInDown" data-wow-delay="0.2s">   </p>
                <h4 className="wow fadeInDown" data-wow-delay="0.4s">2WinChance </h4>
              </div>
              <div className="col-lg-4 col-md-12 col-sm-12">
                <ul>
                  <li className="on wow fadeInLeft" data-wow-delay="0.6s">
                    <a title="contact">
                      <dl>
                        <dt>Esports <br className="pc" />/ </dt>
                        <dd>     ,  .       
                            .</dd>
                      </dl>
                    </a>
                  </li>
                  <li className="wow fadeInLeft" data-wow-delay="0.7s">
                    <a title="contact">
                      <dl>
                        <dt>  <br className="pc" /> </dt>
                        <dd>Esports   ()   ,     .   
                          /        .</dd>
                      </dl>
                    </a>
                  </li>
                  <li className="wow fadeInLeft" data-wow-delay="0.8s">
                    <a title="contact">
                      <dl>
                        <dt> <br className="pc" /> </dt>
                        <dd>( )    ,    .   
                                 .</dd>
                      </dl>
                    </a>
                  </li>
                </ul>
              </div>
              <div className="col-lg-8 col-md-12 col-sm-12 wow fadeInRight" data-wow-delay="1.0s">
                <img src={`${process.env.PUBLIC_URL}/images/test/game1.jpg`} alt="Esports Betting Game " />
              </div>
            </div>
            <div className="row sec">
              <div className="col-lg-8 col-md-12 col-sm-12 wow fadeInLeft" data-wow-delay="0.4s">
                <img src={`${process.env.PUBLIC_URL}/images/game/game4.png`} alt="  " />
              </div>
              <div className="col-lg-4 col-md-12 col-sm-12">
                <dl>
                  <dt className="wow fadeInRight" data-wow-delay="0.6s">  </dt>
                  <dd className="wow fadeInRight" data-wow-delay="0.7s">     </dd>
                  <dd className="wow fadeInRight" data-wow-delay="0.8s">    </dd>
                  <dd className="wow fadeInRight" data-wow-delay="0.9s">    </dd>
                </dl>
              </div>
            </div>
          </div>
        </section>

        <section id="platform" className="sub_cont">
          <div className="container">
            <div className="row">
              <div className="col-12">
                <p className="title wow fadeInDown" data-wow-delay="0.2s">2WinChance </p>
                <h4 className="wow fadeInDown" data-wow-delay="0.4s">   </h4>
                <img src={`${process.env.PUBLIC_URL}/images/game/platform.png`} alt="   " className="wow fadeInDown"
                  data-wow-delay="0.6s" />
              </div>
              <div className="col-lg-4 col-md-6 col-sm-12 wow fadeInUp" data-wow-delay="0.8s">
                <div className="box">
                  <p>1</p>
                  <dl>
                    <dt>Esports 2WinChance Platform </dt>
                    <dd>     Esports   </dd>
                  </dl>
                </div>
              </div>
              <div className="col-lg-4 col-md-6 col-sm-12 wow fadeInUp" data-wow-delay="0.9s">
                <div className="box">
                  <p>2</p>
                  <dl>
                    <dt>  </dt>
                    <dd>  </dd>
                    <dd> Esports        </dd>
                  </dl>
                </div>
              </div>
              <div className="col-lg-4 col-md-6 col-sm-12 wow fadeInUp" data-wow-delay="1.0s">
                <div className="box">
                  <p>3</p>
                  <dl>
                    <dt>  </dt>
                    <dd>Esports    SEA()</dd>
                    <dd>    </dd>
                  </dl>
                </div>
              </div>
            </div>
          </div>
        </section>

        <section id="gameContact" className="sub_contact">
          <div className="container">
            <div className="row">
              <h4 className="wow fadeInDown" data-wow-delay="0.4s">2WinChance  ?</h4>
              {/* <button
    title="contact" 
    className="btn_contact wow fadeInUp"
    data-wow-delay="0.8s"
    onClick={() => {
      onclickModal()
    }}
  >
    CONTACT US
      </button> */}
              <Link to={"/contact"} className="btn_contact wow fadeInUp" data-wow-delay="0.8s">CONTACT US</Link>
            </div>
          </div>
        </section>
      </div>
      {isOpenContact ? (
              <ContactModal setIsOpenContact={setIsOpenContact} />
            ) : null}
      <footer>
        <div className="container">
          <Footer />
        </div>
      </footer>
    </div>
  )
}

export default Game